import { Log } from '@fanfanlo'
import { triggerTimeoutUtils } from '../triggers/time/timeout/ITriggerTimeout.inft'
import { triggerWhenActivityInUtils } from '../triggers/app/when-activity-in/ITriggerWhenActivityIn'
import { triggerNodeTriggerUtils } from '../triggers/a11y/node-trigger/ITriggerNodeTrigger'
import { triggerScriptUtils } from '../triggers/script/script-trigger/ITriggerScript.inft'

const fileLog = new Log(false, 'initTriggers_f')
fileLog.pause = true
fileLog.childrenPaused = true

/**
 *  Triggers
 *  triggerUtils.init() 
 */
export function initTriggers() {

    triggerTimeoutUtils.init()

    triggerWhenActivityInUtils.init()

    triggerNodeTriggerUtils.init()

    triggerScriptUtils.init()

    fileLog.log('[initTriggers]  trigger types ')
}
