/**
 * DroidSelector
 * selector
 */
export enum SelectorOperation {
    // ====================  ====================
    TEXT = 'text',
    TEXT_CONTAINS = 'textContains',
    TEXT_STARTS_WITH = 'textStartsWith',
    TEXT_ENDS_WITH = 'textEndsWith',
    TEXT_MATCHES = 'textMatches',

    // ==================== ID ====================
    ID = 'id',
    ID_CONTAINS = 'idContains',
    ID_STARTS_WITH = 'idStartsWith',
    ID_ENDS_WITH = 'idEndsWith',
    ID_MATCHES = 'idMatches',

    // ====================  ====================
    CLASS_NAME = 'className',
    CLASS_NAME_CONTAINS = 'classNameContains',
    CLASS_NAME_STARTS_WITH = 'classNameStartsWith',
    CLASS_NAME_ENDS_WITH = 'classNameEndsWith',
    CLASS_NAME_MATCHES = 'classNameMatches',

    // ====================  ====================
    DESC = 'desc',
    DESC_CONTAINS = 'descContains',
    DESC_STARTS_WITH = 'descStartsWith',
    DESC_ENDS_WITH = 'descEndsWith',
    DESC_MATCHES = 'descMatches',

    // ====================  ====================
    PACKAGE_NAME = 'packageName',
    PACKAGE_NAME_CONTAINS = 'packageNameContains',
    PACKAGE_NAME_STARTS_WITH = 'packageNameStartsWith',
    PACKAGE_NAME_ENDS_WITH = 'packageNameEndsWith',
    PACKAGE_NAME_MATCHES = 'packageNameMatches',

    // ====================  ====================
    CLICKABLE = 'clickable',
    LONG_CLICKABLE = 'longClickable',
    SCROLLABLE = 'scrollable',
    ENABLED = 'enabled',
    CHECKABLE = 'checkable',
    CHECKED = 'checked',
    FOCUSABLE = 'focusable',
    FOCUSED = 'focused',
    VISIBLE_TO_USER = 'visibleToUser',
    SELECTED = 'selected',
    PASSWORD = 'password',

    // ====================  ====================
    DEPTH = 'depth',
    INDEX_IN_PARENT = 'indexInParent',
    CHILD_COUNT = 'childCount',

    // ====================  ====================
    BOUNDS = 'bounds',
    BOUNDS_INSIDE = 'boundsInside',
    BOUNDS_CONTAINS = 'boundsContains',

    // ====================  ====================
    ALGORITHM = 'algorithm'
}

/**
 * selector
 */
export interface SelectorOperationItem {
    /**  */
    method: SelectorOperation
    /**  */
    value: any
}

/**
 * Selector
 */
export interface NodeSelectorChain {
    /**  */
    operations: SelectorOperationItem[]
}
