/**
 * projectUtils - 
 *  v2  (IProject, ITrigger, IAction )
 */

import { Log } from "@fanfanlo";
import { InjectKeyType } from "@app/static";
import { IProject, IPermissionData, getActionUtils, getTriggerUtils } from '@droid/project-v2-interface';

const fileLog = new Log(false, 'project-utils_f')
fileLog.pause = false
fileLog.childrenPaused = false

/**
 * Project
 * @param project 
 * @returns 
 */
function getProjectPermissions(project: IProject): string[] {
    const fnLog = fileLog.sub(false, 'getProjectPermissions_fn')
    // fnLog.pause = true;
    fnLog.log('[getProjectPermissions] , projectUlid:', project.ulid);

    const permissions = new Set<string>();

    if (!project.children || project.children.length === 0) {
        fnLog.log('[getProjectPermissions] project.children ');
        return [];
    }
    fnLog.log('[getProjectPermissions] project.children.length:', project.children.length);

    //  triggerActionGroup
    for (let g = 0; g < project.children.length; g++) {
        const triggerActionGroup = project.children[g];
        fnLog.log('[getProjectPermissions]  triggerActionGroup[', g, '], ulid:', triggerActionGroup.ulid);

        const triggerActionChildren = triggerActionGroup.children;
        if (!triggerActionChildren) {
            fnLog.log('[getProjectPermissions] triggerActionGroup[', g, '].children ');
            continue;
        }

        for (let i = 0; i < triggerActionChildren.length; i++) {
            const triggerAction = triggerActionChildren[i];
            fnLog.log('[getProjectPermissions] triggerAction[', i, ']');

            const triggerGroup = triggerAction.triggerGroup;
            if (triggerGroup) {
                const triggerChildren = triggerGroup.children;
                if (triggerChildren) {
                    for (let j = 0; j < triggerChildren.length; j++) {
                        const trigger = triggerChildren[j];
                        if (trigger.permissions) {
                            fnLog.log('[getProjectPermissions] trigger, :', trigger.permissions.length);
                            for (let k = 0; k < trigger.permissions.length; k++) {
                                const permission = trigger.permissions[k];
                                fnLog.log('[getProjectPermissions] :', permission.name);
                                permissions.add(permission.name);
                            }
                        }
                    }
                }
            }

            const actionGroup = triggerAction.actionGroup;
            if (actionGroup) {
                const actionChildren = actionGroup.children;
                if (actionChildren) {
                    for (let j = 0; j < actionChildren.length; j++) {
                        const action = actionChildren[j];
                        if (action.permissions) {
                            fnLog.log('[getProjectPermissions] action, :', action.permissions.length);
                            for (let k = 0; k < action.permissions.length; k++) {
                                const permission = action.permissions[k];
                                fnLog.log('[getProjectPermissions] :', permission.name);
                                permissions.add(permission.name);
                            }
                        }
                    }
                }
            }
        }
    }

    const result = [...permissions];
    fnLog.log('[getProjectPermissions] , :', result.length);
    return result;
}

/**
 * Project
 * namevaluereason
 *  ( action/trigger  loadI18nTexts)
 */
async function getProjectPermissionsData(project: IProject): Promise<IPermissionData[]> {
    const fnLog = fileLog.sub(false, 'getProjectPermissionsData_fn')
    fnLog.pause = false
    fnLog.log('[getProjectPermissionsData] , projectUlid:', project.ulid);

    if (!project.children || project.children.length === 0) {
        fnLog.log('[getProjectPermissionsData] project.children ');
        return [];
    }
    fnLog.log('[getProjectPermissionsData] project.children.length:', project.children.length);

    // 
    const permissionsMap = new Map<string, IPermissionData>();

    //  triggerActionGroup
    for (let g = 0; g < project.children.length; g++) {
        const triggerActionGroup = project.children[g];
        fnLog.log('[getProjectPermissionsData]  triggerActionGroup[', g, '], ulid:', triggerActionGroup.ulid);

        const triggerActionChildren = triggerActionGroup.children;
        if (!triggerActionChildren) {
            fnLog.log('[getProjectPermissionsData] triggerActionGroup[', g, '].children ');
            continue;
        }

        //  action/trigger  loadI18nTexts 
        fnLog.log('[getProjectPermissionsData] ');

        for (let i = 0; i < triggerActionChildren.length; i++) {
            const triggerAction = triggerActionChildren[i];
            fnLog.log('[getProjectPermissionsData] triggerAction[', i, ']');

            //  trigger  i18n
            const triggerGroup = triggerAction.triggerGroup;
            if (triggerGroup) {
                const triggerChildren = triggerGroup.children;
                if (triggerChildren) {
                    for (let j = 0; j < triggerChildren.length; j++) {
                        const trigger = triggerChildren[j];
                        fnLog.log('[getProjectPermissionsData] trigger[', j, '], type:', trigger.type);

                        const triggerUtils = getTriggerUtils(trigger.type);
                        fnLog.log('[getProjectPermissionsData] triggerUtils :', !!triggerUtils);

                        if (triggerUtils) {
                            const hasLoadI18nTexts = !!triggerUtils.loadI18nTexts;
                            fnLog.log('[getProjectPermissionsData] triggerUtils.loadI18nTexts :', hasLoadI18nTexts);

                            if (hasLoadI18nTexts) {
                                fnLog.log('[getProjectPermissionsData]  triggerUtils.loadI18nTexts');
                                await triggerUtils.loadI18nTexts(trigger);
                                fnLog.log('[getProjectPermissionsData] trigger i18n ');
                            }
                        }
                    }
                }
            }

            //  action  i18n
            const actionGroup = triggerAction.actionGroup;
            if (actionGroup) {
                const actionChildren = actionGroup.children;
                if (actionChildren) {
                    for (let j = 0; j < actionChildren.length; j++) {
                        const action = actionChildren[j];
                        fnLog.log('[getProjectPermissionsData] action[', j, '], type:', action.type);

                        const actionUtils = getActionUtils(action.type);
                        fnLog.log('[getProjectPermissionsData] actionUtils :', !!actionUtils);

                        if (actionUtils) {
                            const hasLoadI18nTexts = !!actionUtils.loadI18nTexts;
                            fnLog.log('[getProjectPermissionsData] actionUtils.loadI18nTexts :', hasLoadI18nTexts);

                            if (hasLoadI18nTexts) {
                                fnLog.log('[getProjectPermissionsData]  actionUtils.loadI18nTexts');
                                await actionUtils.loadI18nTexts(action);
                                fnLog.log('[getProjectPermissionsData] action i18n ');
                            }
                        }
                    }
                }
            }
        }

        fnLog.log('[getProjectPermissionsData] triggerActionGroup[', g, '] i18n , ');

        for (let i = 0; i < triggerActionChildren.length; i++) {
            const triggerAction = triggerActionChildren[i];
            fnLog.log('[getProjectPermissionsData] triggerAction[', i, ']');

            const triggerGroup = triggerAction.triggerGroup;
            if (triggerGroup) {
                const triggerChildren = triggerGroup.children;
                if (triggerChildren) {
                    for (let j = 0; j < triggerChildren.length; j++) {
                        const trigger = triggerChildren[j];
                        if (trigger.permissions) {
                            fnLog.log('[getProjectPermissionsData] trigger, :', trigger.permissions.length);
                            for (let k = 0; k < trigger.permissions.length; k++) {
                                const permission = trigger.permissions[k];
                                fnLog.log('[getProjectPermissionsData] :', permission.name, 'value:', permission.value);
                                if (permission.value) {
                                    permissionsMap.set(permission.value, permission);
                                }
                            }
                        }
                    }
                }
            }

            const actionGroup = triggerAction.actionGroup;
            if (actionGroup) {
                const actionChildren = actionGroup.children;
                if (actionChildren) {
                    for (let j = 0; j < actionChildren.length; j++) {
                        const action = actionChildren[j];
                        if (action.permissions) {
                            fnLog.log('[getProjectPermissionsData] action, :', action.permissions.length);
                            for (let k = 0; k < action.permissions.length; k++) {
                                const permission = action.permissions[k];
                                fnLog.log('[getProjectPermissionsData] :', permission.name, 'value:', permission.value);
                                if (permission.value) {
                                    permissionsMap.set(permission.value, permission);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    const result = Array.from(permissionsMap.values());
    fnLog.log('[getProjectPermissionsData] , :', result.length, JSON.stringify(result));
    return result;
}

/**
 * Project
 *  action  trigger  injects
 * , InjectKeyType  [InjectKeyType, string]
 */
function getProjectInjects(project: IProject): (InjectKeyType | [InjectKeyType, string])[] {
    const fnLog = fileLog.sub(false, 'getProjectInjects_fn')
    fnLog.pause = false;
    fnLog.log('[getProjectInjects] , projectUlid:', project.ulid);

    const injectsArray: (InjectKeyType | [InjectKeyType, string])[] = [];
    const injectKeysSet = new Set<string>();

    if (!project.children || project.children.length === 0) {
        fnLog.log('[getProjectInjects] project.children ');
        return [];
    }
    fnLog.log('[getProjectInjects] project.children.length:', project.children.length);

    //  triggerActionGroup
    for (let g = 0; g < project.children.length; g++) {
        const triggerActionGroup = project.children[g];
        fnLog.log('[getProjectInjects]  triggerActionGroup[', g, '], ulid:', triggerActionGroup.ulid);

        const triggerActionChildren = triggerActionGroup.children;
        if (!triggerActionChildren) {
            fnLog.log('[getProjectInjects] triggerActionGroup[', g, '].children ');
            continue;
        }

        fnLog.log('[getProjectInjects] triggerActionChildren:', triggerActionChildren.length);

        for (let i = 0; i < triggerActionChildren.length; i++) {
            const triggerAction = triggerActionChildren[i];
            fnLog.log('[getProjectInjects] triggerAction[', i, ']');

            const triggerGroup = triggerAction.triggerGroup;
            if (triggerGroup) {
                const triggerChildren = triggerGroup.children;
                if (triggerChildren) {
                    fnLog.log('[getProjectInjects] triggerChildren:', triggerChildren.length);
                    for (let j = 0; j < triggerChildren.length; j++) {
                        const trigger = triggerChildren[j];
                        fnLog.log('[getProjectInjects] trigger[', j, ']');
                        if (trigger.injects) {
                            fnLog.log('[getProjectInjects] triggerinjects, :', trigger.injects.length);
                            for (let k = 0; k < trigger.injects.length; k++) {
                                const injectConfig = trigger.injects[k];
                                fnLog.log('[getProjectInjects] inject[', k, ']:', JSON.stringify(injectConfig));

                                let injectKey: string;
                                let varName: string | undefined;

                                // 
                                const isArray = Array.isArray(injectConfig);
                                fnLog.log('[getProjectInjects] injectConfig:', isArray);

                                if (isArray) {
                                    // : [key, varName]
                                    injectKey = injectConfig[0];
                                    varName = injectConfig[1];
                                    fnLog.log('[getProjectInjects] , key:', injectKey, ', varName:', varName);
                                } else {
                                    // : { key, varName }
                                    injectKey = injectConfig.key;
                                    varName = injectConfig.varName;
                                    fnLog.log('[getProjectInjects] , key:', injectKey, ', varName:', varName);
                                }

                                const hasKey = injectKeysSet.has(injectKey);
                                fnLog.log('[getProjectInjects] :', hasKey);

                                if (!hasKey) {
                                    if (varName) {
                                        fnLog.log('[getProjectInjects] inject()');
                                        injectsArray.push([injectKey as InjectKeyType, varName]);
                                    } else {
                                        fnLog.log('[getProjectInjects] inject()');
                                        injectsArray.push(injectKey as InjectKeyType);
                                    }
                                    injectKeysSet.add(injectKey);
                                }
                            }
                        }
                    }
                }
            }

            const actionGroup = triggerAction.actionGroup;
            if (actionGroup) {
                const actionChildren = actionGroup.children;
                if (actionChildren) {
                    fnLog.log('[getProjectInjects] actionChildren:', actionChildren.length);
                    for (let j = 0; j < actionChildren.length; j++) {
                        const action = actionChildren[j];
                        fnLog.log('[getProjectInjects] action[', j, ']');
                        if (action.injects) {
                            fnLog.log('[getProjectInjects] actioninjects, :', action.injects.length);
                            for (let k = 0; k < action.injects.length; k++) {
                                const injectConfig = action.injects[k];
                                fnLog.log('[getProjectInjects] inject[', k, ']:', JSON.stringify(injectConfig));

                                let injectKey: string;
                                let varName: string | undefined;

                                // 
                                const isArray = Array.isArray(injectConfig);
                                fnLog.log('[getProjectInjects] injectConfig:', isArray);

                                if (isArray) {
                                    // : [key, varName]
                                    injectKey = injectConfig[0];
                                    varName = injectConfig[1];
                                    fnLog.log('[getProjectInjects] , key:', injectKey, ', varName:', varName);
                                } else {
                                    // : { key, varName }
                                    injectKey = injectConfig.key;
                                    varName = injectConfig.varName;
                                    fnLog.log('[getProjectInjects] , key:', injectKey, ', varName:', varName);
                                }

                                const hasKey = injectKeysSet.has(injectKey);
                                fnLog.log('[getProjectInjects] :', hasKey);

                                if (!hasKey) {
                                    if (varName) {
                                        fnLog.log('[getProjectInjects] inject()');
                                        injectsArray.push([injectKey as InjectKeyType, varName]);
                                    } else {
                                        fnLog.log('[getProjectInjects] inject()');
                                        injectsArray.push(injectKey as InjectKeyType);
                                    }
                                    injectKeysSet.add(injectKey);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    fnLog.log('[getProjectInjects] , :', injectsArray.length);
    return injectsArray;
}

/**
 *  TriggerAction
 *  TriggerAction : triggerGroup.children  actionGroup.children 
 * @param project 
 * @returns  TriggerAction 
 */
function cleanEmptyTriggerActions(project: IProject): number {
    const fnLog = fileLog.sub(false, 'cleanEmptyTriggerActions_fn')
    fnLog.pause = false
    fnLog.log('[cleanEmptyTriggerActions]  TriggerAction')

    if (!project.children || project.children.length === 0) {
        fnLog.log('[cleanEmptyTriggerActions] project.children ,')
        return 0
    }
    fnLog.log('[cleanEmptyTriggerActions] project.children.length:', project.children.length);

    let totalRemovedCount = 0;

    //  triggerActionGroup
    for (let g = 0; g < project.children.length; g++) {
        const triggerActionGroup = project.children[g];
        fnLog.log('[cleanEmptyTriggerActions]  triggerActionGroup[', g, '], ulid:', triggerActionGroup.ulid);

        const triggerActionChildren = triggerActionGroup.children;
        if (!triggerActionChildren) {
            fnLog.log('[cleanEmptyTriggerActions] triggerActionGroup[', g, '].children ,');
            continue;
        }

        // 
        const originalCount = triggerActionChildren.length
        fnLog.log('[cleanEmptyTriggerActions]  TriggerAction :', originalCount)

        //  triggerAction
        fnLog.log('[cleanEmptyTriggerActions] ')
        const filteredList = triggerActionChildren.filter((triggerAction, index) => {
            fnLog.log('[cleanEmptyTriggerActions]  triggerAction[', index, '] ulid:', triggerAction.ulid)

            //  triggerGroup
            const triggerGroup = triggerAction.triggerGroup;
            const hasTriggerGroup = triggerGroup ? true : false
            fnLog.log('[cleanEmptyTriggerActions] triggerAction[', index, '] triggerGroup :', hasTriggerGroup)

            let triggerCount = 0;
            if (triggerGroup && triggerGroup.children) {
                triggerCount = triggerGroup.children.length;
            }
            fnLog.log('[cleanEmptyTriggerActions] triggerAction[', index, '] trigger :', triggerCount)

            //  actionGroup
            const actionGroup = triggerAction.actionGroup;
            const hasActionGroup = actionGroup ? true : false
            fnLog.log('[cleanEmptyTriggerActions] triggerAction[', index, '] actionGroup :', hasActionGroup)

            let actionCount = 0;
            if (actionGroup && actionGroup.children) {
                actionCount = actionGroup.children.length;
            }
            fnLog.log('[cleanEmptyTriggerActions] triggerAction[', index, '] action :', actionCount)

            // (trigger  action )
            const hasTriggers = triggerCount > 0
            const hasActions = actionCount > 0

            fnLog.log('[cleanEmptyTriggerActions] triggerAction[', index, '] hasTriggers:', hasTriggers)
            fnLog.log('[cleanEmptyTriggerActions] triggerAction[', index, '] hasActions:', hasActions)

            //  trigger  action 
            const shouldKeep = hasTriggers || hasActions
            fnLog.log('[cleanEmptyTriggerActions] triggerAction[', index, '] shouldKeep:', shouldKeep)

            if (!shouldKeep) {
                fnLog.log('[cleanEmptyTriggerActions]  triggerAction[', index, '] ulid:', triggerAction.ulid)
            } else {
                fnLog.log('[cleanEmptyTriggerActions]  triggerAction[', index, '] ulid:', triggerAction.ulid)
            }

            return shouldKeep
        })

        // 
        fnLog.log('[cleanEmptyTriggerActions]  children')
        triggerActionGroup.children = filteredList
        fnLog.log('[cleanEmptyTriggerActions] children :', filteredList.length)

        const removedCount = originalCount - filteredList.length
        totalRemovedCount += removedCount
        fnLog.log('[cleanEmptyTriggerActions] triggerActionGroup[', g, '] ', removedCount, ' TriggerAction')
    }

    fnLog.log('[cleanEmptyTriggerActions] ', totalRemovedCount, ' TriggerAction')
    return totalRemovedCount
}

// ====================  ====================

export const projectUtils = {
    getProjectPermissions,
    getProjectPermissionsData,
    getProjectInjects,
    cleanEmptyTriggerActions
};
