import { ulid } from 'ulid';
import _ from 'lodash';

export enum Gravity {
  // 
  LEFT = 3,             // 
  RIGHT = 5,            // 
  END = 8388613,        // RTL 
  START = 8388608,      // RTL 

  // 
  TOP = 48,             // 
  BOTTOM = 80,          // 

  // 
  CENTER = 17,                   // 
  CENTER_HORIZONTAL = 1,         // 
  CENTER_VERTICAL = 16,          // 

  // 
  NO_GRAVITY = 0,      // 
}

// ============ Gravity  ============

/**
 *  Gravity 
 *  Select 
 * @param t , Gravity 
 * @returns Gravity , value  label
 */
export function getGravityOptions(t?: (key: string) => string): Array<{ value: Gravity; label: string }> {
  const gravityKeys: Gravity[] = [
    Gravity.LEFT,
    Gravity.RIGHT,
    Gravity.START,
    Gravity.END,
    Gravity.TOP,
    Gravity.BOTTOM,
    Gravity.CENTER,
    Gravity.CENTER_HORIZONTAL,
    Gravity.CENTER_VERTICAL,
    Gravity.NO_GRAVITY,
  ];

  return gravityKeys.map(gravity => ({
    value: gravity,
    label: t ? t(`content.Gravity.${Gravity[gravity]}`) : Gravity[gravity],
  }));
}

