export function getDefaulWindowConf(): WindowConf {
  return JSON.parse(JSON.stringify(createDefaultWindowConf()));
}

export function createWindowConfCopy(conf: any) {
  return _.merge({}, createDefaultWindowConf(), conf);
}

// ============ createDefault  ============

export function createDefaultWebViewJsParams(): WebViewJsParams {
  return {
    fitContentWidth: 0,
    fitContentHeight: 0,
    javaToJsDataString: ``,
  };
}

export function createDefaultWebViewConf(): WebViewConf {
  return {
    backgroundColor: 0,
  };
}

export function createDefaultGravityInfo(): GravityInfo {
  return {
    gravity: Gravity.START,
  };
}

export function createDefaultBorderInfo(): BorderInfo {
  return {
    left: 0,
    top: 0,
    right: 0,
    bottom: 0,
  };
}

export function createDefaultMatchParentInfo(): MatchParentInfo {
  return {
    widthMatch: false,
    heightMatch: false,
  };
}

export function createDefaultHtmlConf(): HtmlConf {
  return {
    url: ``,
    webViewJsParams: createDefaultWebViewJsParams(),
    webViewConf: createDefaultWebViewConf(),
    jsBridgeName: 'android',
    webViewJsName: 'AutoWebViewJs',
  };
}

export function createDefaultFloatConf(): FloatConf {
  return {
    positionSaveName: '${positionSaveName}',
    draggable: true,
    showPattern: FloatShowPattern.ALL_TIME,
    hasEditText: true,
    sidePattern: FloatSidePattern.DEFAULT,
    gravity: createDefaultGravityInfo(),
    layoutChangedGravity: Gravity.START,
    border: createDefaultBorderInfo(),
    matchParent: createDefaultMatchParentInfo(),
  };
}

export function createDefaultFloatWindowConf(): FloatWindowConf {
  return {
    floatConf: createDefaultFloatConf(),
  };
}

export function createDefaultDialogWindowConf(): DialogWindowConf {
  return {
    viewLayoutParams: {},
    theme: '',
  };
}

export function createDefaultSysWindowConf(): SysWindowConf {
  return {
    viewLayoutParams: {},
    listenBackKey: false,
  };
}

export function createDefaultWindowConf(): WindowConf {
  return {
    tag: '',
    contentType: ContentType.Html,
    windowType: WindowType.Float,
    htmlConf: createDefaultHtmlConf(),
    floatWindowConf: createDefaultFloatWindowConf(),
    windowId: ulid(),
    forceDismissWindowTime: 0,
  };
}



export function getWindowDismissEventType(id:string): String {
  return `window-${id}-dismiss`;
}
export function getWindowOpenedEventType(id:string): String {
  return `window-${id}-opened`;
}
export function getWindowHideEventType(id:string): String {
  return `window-${id}-hide`;
}