import { ulid } from "ulid"
import { createActionUtils, IAction, IActionJavaData, IActionWebConfig } from "@droid/project-v2-interface"
import { a11yActionNamespace } from "../a11y-namespace"
import { UI } from "./UI"
import { ManifestPermission } from "@droid/android"
import { RhinoInjectNames } from "@app/static"
import { Log } from "@fanfanlo"

const fileLog = new Log(false, '[IActionNodeAction]')
fileLog.pause = true
fileLog.childrenPaused = true

export const actionNodeActionType = `${a11yActionNamespace}node-action`
const type = actionNodeActionType
fileLog.log('[IActionNodeAction] type:', type)

/**
 * 
 */
export enum ActionType {
    click = 'click',
    longClick = 'longClick',
    scroll = 'scroll',
    setText = 'setText',
    focus = 'focus'
}

/**
 * 
 */
export interface ContinuousClickConfig {
    /**  */
    enableContinuousClick: boolean
    /** () */
    baseInterval: number
    /** (,) */
    intervalVariation: number
}

/**
 * 
 */
export interface ClickConfig {
    /** (node.click) */
    useGlobalClick: boolean
    /**  */
    continuousClickConfig?: ContinuousClickConfig
}

/**
 * 
 */
export interface ScrollConfig {
    /**  */
    direction: 'forward' | 'backward'
}

/**
 * 
 */
export interface TextConfig {
    /**  */
    text: string
}

/**
 * NodeAction  Java 
 */
export interface IActionNodeActionJavaData extends IActionJavaData {
    /**  */
    actionType: ActionType
    /**  (actionTypeclick) */
    clickConfig?: ClickConfig
    /**  (actionTypescroll) */
    scrollConfig?: ScrollConfig
    /**  (actionTypesetText) */
    textConfig?: TextConfig
}

/**
 * NodeAction 
 */
export interface IActionNodeActionWebConfig extends IActionWebConfig {
    // Node Action  Web 
}

export interface IActionNodeActionData extends IAction<IActionNodeActionJavaData, IActionNodeActionWebConfig> {
    javaData: IActionNodeActionJavaData
}

/**
 *  NodeAction 
 */
function createDefaultData(): IActionNodeActionData {
    fileLog.log('[createDefaultData] ')

    const ulidValue = ulid()
    fileLog.log('[createDefaultData] ulid:', ulidValue)

    const permissions = [
        {
            name: '',
            value: ManifestPermission.BIND_ACCESSIBILITY_SERVICE,
            reason: ''
        }
    ]
    fileLog.log('[createDefaultData] :', permissions.length)

    const injects: ([typeof RhinoInjectNames.a11yModel, string])[] = [
        [RhinoInjectNames.a11yModel, 'a11yModel']
    ]
    fileLog.log('[createDefaultData] :', injects.length)

    const defaultData: IActionNodeActionData = {
        
        ulid: ulidValue,
        uuid: '',
        ownerUuid: '',
        currentVersion: 0,
        type,
        name: 'actionNodeAction',
        permissions,
        injects,
        javaData: {
            actionType: ActionType.click,
            clickConfig: {
                useGlobalClick: true,
                continuousClickConfig: {
                    enableContinuousClick: false,
                    baseInterval: 300,
                    intervalVariation: 50
                }
            }
        },
        createdAt: 0,
        updatedAt: 0,
        authorUuid: '',
    }
    fileLog.log('[createDefaultData] defaultData:', JSON.stringify(defaultData))
    return defaultData
}

/**
 * 
 */
function init() {
    fileLog.log('[init] node-action ')
}

/**
 * NodeAction 
 */
export const actionNodeActionUtils = createActionUtils(type, init, createDefaultData, UI)
