export const scriptActionSimpleTemplate = `// 
function execute(context) {
    console.log('')
    // 
}

export { execute }`

export const scriptActionAdvancedTemplate = `// 
function execute(context) {
    const logger = context.logger
    logger.log('')

    // 
    const node = context.node
    if (node) {
        logger.log(':', node)
        // 
        node.click()
    }
}

function cleanup(context) {
    context.logger.log('')
}

export { execute, cleanup }`

export const scriptActionFullTemplate = scriptActionAdvancedTemplate
