import { ulid } from "ulid"
import { ITrigger, ITriggerJavaData, ITriggerWebConfig, createTriggerUtils } from "@droid/project-v2-interface"
import { scriptTriggerNamespace } from "../script-namespace"
import { UI } from "./UI"
import { Log } from "@fanfanlo"

const fileLog = new Log(false, '[ITriggerScript]')
fileLog.pause = true
fileLog.childrenPaused = true

const type = `${scriptTriggerNamespace}script`
fileLog.log('[ITriggerScript] type:', type)

export interface ITriggerScriptJavaData extends ITriggerJavaData {
    script: string
    scriptMode: 'simple' | 'advanced' | 'full'
}

export interface ITriggerScriptWebConfig extends ITriggerWebConfig {
    // Script Trigger  Web 
}

export interface ITriggerScriptData extends ITrigger<ITriggerScriptJavaData, ITriggerScriptWebConfig> {
    javaData: ITriggerScriptJavaData
}

function createDefaultData(): ITriggerScriptData {
    fileLog.log('[ITriggerScript.createDefaultData] ')

    const ulidValue = ulid()
    fileLog.log('[ITriggerScript.createDefaultData] ulid:', ulidValue)

    const defaultData: ITriggerScriptData = {
        
        ulid: ulidValue,
        uuid: '',
        ownerUuid: '',
        currentVersion: 0,
        type,
        name: 'triggerScript',
        javaData: {
            script: '',
            scriptMode: 'simple'
        },
        createdAt: 0,
        updatedAt: 0,
        authorUuid: '',
    }
    fileLog.log('[ITriggerScript.createDefaultData] defaultData:', JSON.stringify(defaultData))
    return defaultData
}

function init() {
    fileLog.log('[ITriggerScript.init] ')
}

export const triggerScriptUtils = createTriggerUtils(type, init, createDefaultData, UI)
