import { ulid } from "ulid"
import { createActionUtils, IAction, IActionJavaData, IActionWebConfig } from "@droid/project-v2-interface"
import { appActionNamespace } from "../app-namespace"
import { IActionOpenAppUI } from "./IActionOpenApp.UI"
import { ManifestChinesePhoneSpecialPermission, ManifestPermission } from "@droid/android"
import { RhinoInjectNames } from "@app/static"
import { getI18nT, loadI18nValue, Log } from '@fanfanlo'
import { Namespace } from "i18next"
import { IPermissionData } from '@droid/project-v2-interface'

const fileLog = new Log(false, '[IActionOpenApp]')
fileLog.pause = true
fileLog.childrenPaused = true

const actionType = `${appActionNamespace}openApp`
fileLog.log('[IActionOpenApp] type:', actionType)

const i18nNamespace = 'droid-project-v2-ext/actions/app/action-open-app/content'

export interface IPermissionI18nData {
    reasonI18nNs: string
    reasonI18nKey: string
}

export interface IActionOpenAppJavaData extends IActionJavaData {
    packageName: string
}

export interface IActionOpenAppWebConfig extends IActionWebConfig {
    permissionsMap: Record<string, IPermissionI18nData>
}

export interface IActionOpenAppData extends IAction<IActionOpenAppJavaData, IActionOpenAppWebConfig> {
    javaData: IActionOpenAppJavaData
}

function createDefaultData(): IActionOpenAppData {
    fileLog.log('[createDefaultData] ')

    const ulidValue = ulid()
    fileLog.log('[createDefaultData] ulid:', ulidValue)

    const permissions: IPermissionData[] = [
        {
            name: '',
            value: ManifestPermission.BIND_ACCESSIBILITY_SERVICE,
            reason: ''
        },
        {
            name: '',
            value: ManifestChinesePhoneSpecialPermission.BACKGROUND_POPUP,
            reason: ''
        }
    ]
    const permissionsMap = {
        [ManifestPermission.BIND_ACCESSIBILITY_SERVICE]: {
            reasonI18nNs: i18nNamespace,
            reasonI18nKey: ManifestPermission.BIND_ACCESSIBILITY_SERVICE
        },
        [ManifestChinesePhoneSpecialPermission.BACKGROUND_POPUP]: {
            reasonI18nNs: i18nNamespace,
            reasonI18nKey: ManifestChinesePhoneSpecialPermission.BACKGROUND_POPUP
        },
    }
    fileLog.log('[createDefaultData] :', permissions.length)

    const injects: ([typeof RhinoInjectNames.a11yModel, string])[] = [
        [RhinoInjectNames.a11yModel, 'a11yModel']
    ]
    fileLog.log('[createDefaultData] :', injects.length)

    fileLog.log('[createDefaultData] permissionsMap=', JSON.stringify(permissionsMap))
    const defaultData = {
        
        ulid: ulidValue,
        uuid: '',
        ownerUuid: '',
        currentVersion: 0,
        type: actionType,
        name: 'actionOpenApp',
        permissions,
        injects,
        javaData: {
            packageName: ''
        },
        webConfig: {
            permissionsMap
        },
        createdAt: 0,
        updatedAt: 0,
        authorUuid: '',
    }
    fileLog.log('[createDefaultData] defaultData:', JSON.stringify(defaultData))
    return defaultData
}

function init() {
    fileLog.log('[init] actionOpenApp ')
}

// 
async function loadI18nTexts(data: IActionOpenAppData): Promise<void> {
    fileLog.log('[loadI18nTexts] ')
    fileLog.log('[loadI18nTexts] data:', JSON.stringify(data))

    if (!data.permissions) {
        fileLog.log('[loadI18nTexts] data.permissions  undefined, ')
        return
    }
    if (data.permissions.length === 0) {
        fileLog.log('[loadI18nTexts] data.permissions  0, ')
        return
    }

    //  IActionOpenApp  ()
    fileLog.log('[loadI18nTexts]  IActionOpenApp ')
    const actionT = await getI18nT('droid-project-v2-ext/actions/app/action-open-app/content')
    fileLog.log('[loadI18nTexts] IActionOpenApp ')

    // 
    const permissionsLength = data.permissions.length
    fileLog.log('[loadI18nTexts] :', permissionsLength)
    for (let i = 0; i < permissionsLength; i++) {
        const permission = data.permissions[i]
        fileLog.log('[loadI18nTexts] :', i, 'value:', permission.value)

        const permissionI18n = data.webConfig.permissionsMap[permission.value]
        fileLog.log('[loadI18nTexts] permissionI18n=', JSON.stringify(permissionI18n))
        if (permissionI18n) {
            const ns = permissionI18n.reasonI18nNs
            const key = permissionI18n.reasonI18nKey
            fileLog.log('[loadI18nTexts] permission i18n ns key', ns, key)
            if (ns && key) {
                const fullKey = `IActionOpenApp.permission.${key}`
                const reason = await loadI18nValue(ns as Namespace, fullKey)
                if (reason != fullKey) {
                    permission.reason = reason
                    fileLog.log('[loadI18nTexts] i18n', permission.reason)
                } else {
                    fileLog.log('[loadI18nTexts] i18nkey=', fullKey)
                }
            }
        }
    }

    fileLog.log('[loadI18nTexts] ')
}

export const actionOpenAppUtils = createActionUtils(actionType, init, createDefaultData, IActionOpenAppUI)

//  loadI18nTexts 
actionOpenAppUtils.loadI18nTexts = loadI18nTexts
