import { Box, Grid2, Typography } from "@mui/material"
import { actionAccessibilityNodeReaderUtils, IActionAccessibilityNodeReaderData } from "./IActionAccessibilityNodeReader.inft"
import { useTranslation } from '@fanfanlo'
import { Log } from "@fanfanlo"

const fileLog = new Log(false, 'IActionAccessibilityNodeReader_UI')
// fileLog.pause = true
// fileLog.childrenPaused = true

export function IActionAccessibilityNodeReaderUI(data: IActionAccessibilityNodeReaderData) {
    const uiLog = fileLog.sub(false, 'IActionAccessibilityNodeReaderUI_ui')
    // uiLog.pause = true
    // uiLog.childrenPaused = true

    const { t } = useTranslation('droid-project-v2-ext/actions/float-window/action-accessibility-node-reader/content')
    uiLog.log('rendering, data:', data)
    const windowConf = data.javaData.windowConf
    uiLog.log('windowConf:', windowConf)

    function getDisplayText(): string {
        const fnLog = uiLog.sub(false, 'getDisplayText_fn')
        // fnLog.pause = true
        // fnLog.childrenPaused = true

        fnLog.log('calculating display text')

        if (windowConf.description && windowConf.description.trim()) {
            fnLog.log('using description:', windowConf.description)
            return windowConf.description
        }

        if (windowConf.htmlConf?.url && windowConf.htmlConf.url.trim()) {
            fnLog.log('using url:', windowConf.htmlConf.url)
            return windowConf.htmlConf.url
        }

        fnLog.log('using windowId:', windowConf.windowId)
        return windowConf.windowId
    }


    return <Box sx={{ width: '100%' }}>
        <Box>
            <b>{t('IActionAccessibilityNodeReader.title')}</b>
        </Box>
        <Grid2 container sx={{ width: '100%' }}>
            <Grid2 size="grow">
                <Typography sx={{}}>
                    <span style={{ display: "inline-block", textIndent: "10px" }}>
                        {getDisplayText()}
                    </span>
                </Typography>
            </Grid2>
        </Grid2>
    </Box>
}
