import { WindowConf, createDefaultWindowConf } from "@droid/android"
import { ulid } from "ulid"
import { createActionUtils, IAction, IActionJavaData, IActionWebConfig } from "@droid/project-v2-interface"
import { floatWindowActionNamespace } from "../float-window-namespace"
import { IActionAccessibilityNodeReaderUI } from "./IActionAccessibilityNodeReader.UI"
import { Log } from "@fanfanlo"
import { isDev } from "@fanfanlo"

const fileLog = new Log(false, 'IActionAccessibilityNodeReader_inft')
// fileLog.pause = true
// fileLog.childrenPaused = true

const type = `${floatWindowActionNamespace}accessibilityNodeReader`
fileLog.log('type:', type)

export interface IActionAccessibilityNodeReaderJavaData extends IActionJavaData {
    windowConf: WindowConf
}

export interface IActionAccessibilityNodeReaderWebConfig extends IActionWebConfig {
    // Accessibility Node Reader Action  Web 
}

export interface IActionAccessibilityNodeReaderData extends IAction<IActionAccessibilityNodeReaderJavaData, IActionAccessibilityNodeReaderWebConfig> {
    javaData: IActionAccessibilityNodeReaderJavaData
}

function getNodeReaderUrl(): string {
    const fnLog = fileLog.sub(false, 'getNodeReaderUrl_fn')
    // fnLog.pause = true
    // fnLog.childrenPaused = true

    fnLog.log('URL')
    fnLog.log('isDev:', isDev)

    if (isDev) {
        const url = 'https://192.168.177.180:19016/pages/accessibility-node-click-detector/#'
        fnLog.log('dev, url:', url)
        return url
    }

    if (typeof window === 'undefined') {
        fnLog.log(', ')
        return ''
    }

    const currentDomain = window.location.origin
    const url = `${currentDomain}/pages/accessibility-node-click-detector/`
    fnLog.log('dev, currentDomain:', currentDomain)
    fnLog.log('url:', url)
    return url
}

function createDefaultData(): IActionAccessibilityNodeReaderData {
    const fnLog = fileLog.sub(false, 'createDefaultData_fn')
    // fnLog.pause = true
    // fnLog.childrenPaused = true

    fnLog.log('')
    const ulidValue = ulid()
    const windowConf = createDefaultWindowConf()

    const url = getNodeReaderUrl()
    fnLog.log('url:', url)

    if (!windowConf.htmlConf) {
        fnLog.log('htmlConf, htmlConf')
        windowConf.htmlConf = {
            url: '',
            webViewJsParams: {
                fitContentWidth: 0,
                fitContentHeight: 0,
                javaToJsDataString: '',
                fitContentClass: ''
            },
            webViewConf: {
                backgroundColor: 0
            },
            jsBridgeName: 'android',
            webViewJsName: 'AutoWebViewJs',
            html: ''
        }
    }

    windowConf.htmlConf.url = url

    if (!windowConf.htmlConf.webViewJsParams) {
        fnLog.log('webViewJsParams, webViewJsParams')
        windowConf.htmlConf.webViewJsParams = {
            fitContentWidth: 0,
            fitContentHeight: 0,
            javaToJsDataString: '',
            fitContentClass: ''
        }
    }

    windowConf.htmlConf.webViewJsParams.fitContentClass = 'webviewMeatureContainer'
    fnLog.log('fitContentClasswebviewMeatureContainer')

    const data: IActionAccessibilityNodeReaderData = {
        ulid: ulidValue,
        uuid: '',
        ownerUuid: '',
        currentVersion: 0,
        type,
        javaData: {
            windowConf
        },
        createdAt: 0,
        updatedAt: 0,
        authorUuid: '',
    }
    fnLog.log('data:', JSON.stringify(data))
    return data
}

function init() {
    const fnLog = fileLog.sub(false, 'init_fn')
    // fnLog.pause = true
    // fnLog.childrenPaused = true

    fnLog.log('accessibilityNodeReader ')
}

export const actionAccessibilityNodeReaderUtils = createActionUtils(type, init, createDefaultData, IActionAccessibilityNodeReaderUI)
