import { ulid } from "ulid"
import { createTriggerUtils, ITrigger, ITriggerWebConfig, ITriggerJavaData } from "@droid/project-v2-interface"
import { a11yTriggerNamespace } from "../a11y-namespace"
import { UI } from "./UI"
import { ManifestPermission } from "@droid/android"
import { RhinoInjectNames } from "@app/static"
import { NodeSelectorChain } from "./selector-chain"
import { Log } from "@fanfanlo"

const fileLog = new Log(false, '[ITriggerNodeTrigger]')
fileLog.pause = true
fileLog.childrenPaused = true

export const triggerNodeTriggerType = `${a11yTriggerNamespace}node-trigger`
const type = triggerNodeTriggerType
fileLog.log('[ITriggerNodeTrigger] type:', type)

/**
 * NodeTrigger  Java 
 */
export interface ITriggerNodeTriggerJavaData extends ITriggerJavaData {
    /**  */
    pn: string
    /** Activity  */
    an: string
    /**  () */
    cn?: string
    /** Selector */
    selectorChain: NodeSelectorChain
}

/**
 * NodeTrigger 
 */
export interface ITriggerNodeTriggerWebConfig extends ITriggerWebConfig {
    // Node Trigger  Web 
}

export interface ITriggerNodeTriggerData extends ITrigger<ITriggerNodeTriggerJavaData, ITriggerNodeTriggerWebConfig> {
    javaData: ITriggerNodeTriggerJavaData
}

/**
 *  NodeTrigger 
 */
function createDefaultData(): ITriggerNodeTriggerData {
    fileLog.log('[ITriggerNodeTrigger.createDefaultData] ')

    const ulidValue = ulid()
    fileLog.log('[ITriggerNodeTrigger.createDefaultData] ulid:', ulidValue)

    const permissions = [
        {
            name: '',
            value: ManifestPermission.BIND_ACCESSIBILITY_SERVICE,
            reason: ''
        }
    ]
    fileLog.log('[ITriggerNodeTrigger.createDefaultData] :', permissions.length)

    const injects: ([typeof RhinoInjectNames.a11yModel, string])[] = [
        [RhinoInjectNames.a11yModel, 'a11yModel']
    ]
    fileLog.log('[ITriggerNodeTrigger.createDefaultData] :', injects.length)

    const defaultData: ITriggerNodeTriggerData = {
        
        ulid: ulidValue,
        uuid: '',
        ownerUuid: '',
        currentVersion: 0,
        type,
        name: 'triggerNodeTrigger',
        permissions,
        injects,
        javaData: {
            pn: '',
            an: '',
            cn: '',
            selectorChain: {
                operations: []
            }
        },
        createdAt: 0,
        updatedAt: 0,
        authorUuid: '',
    }
    fileLog.log('[ITriggerNodeTrigger.createDefaultData] defaultData:', JSON.stringify(defaultData))
    return defaultData
}

/**
 * 
 */
function init() {
    fileLog.log('[ITriggerNodeTrigger.init] ')
}

/**
 * NodeTrigger 
 */
export const triggerNodeTriggerUtils = createTriggerUtils<ITriggerNodeTriggerData>(type, init, createDefaultData, UI)
